import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Popup from "../../components/Popup";
import axios from "axios";
import music from "../../assets/message.mp3";
import { getAllcatalogue } from "../../store/catalogueSlice";
import io from "socket.io-client";
import Modal from "../../components/Modal/Modal";
import ReactLoading from "react-loading";
import { throttle } from "lodash";
import LotListComponent from "../../components/reuseable/LotListComponent";
import { useNavigate } from "react-router-dom";

const socket = io.connect(process.env.REACT_APP_SERVER_URL);
const Bidders = () => {
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const dispatch = useDispatch();
  const { auction_data } = useSelector((state) => state.auction);
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);
  const [amountBid, setAmountbid] = useState([]);
  const [manualbidIndex, setManualbidindex] = useState("");
  const [checkedItemName, setCheckedItemName] = useState("");
  const [checkedItemid, setCheckedItemid] = useState("");
  const [activebroker, setActivebroker] = useState();
  const [list, setList] = useState([]);
  const [rawlist, setrawList] = useState([]);
  const [buttonLoading, setbuttonLoading] = useState(false);
  const [disabledButtons, setDisabledButtons] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(null);
  const [startIndex, setStartIndex] = useState(0);
  const [endIndex, setEndIndex] = useState(1);
  const [activeUsers, setActiveUsers] = useState(0);
  const [visible, setVisible] = useState(false);
  const [showPopup, setShowPopup] = useState(true);
  const [time, setTime] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [loading, setLoading] = useState(false);
  const [bidloading, setbidLoading] = useState(false);
  const [manualmode, setManualmode] = useState(false);

  //manual bid inpput data get
  const handleInputChange = (event, index) => {
    const { name, value } = event.target;
    setAmountbid((prevState) => {
      const updatedState = [...prevState];
      updatedState[index] = { ...updatedState[index], [name]: value };
      return updatedState;
    });
    setManualbidindex(index);
    // console.log(manualbidIndex);
  };

  //bidder modal
  const openLot = async () => {
    await axios
      .get(`${API_URL}/getselectbroker`)
      .then((response) => {
        socket.emit("user", response.data[0].current_brokers);
      })
      .catch((error) => {
        console.log(error);
      });
    dispatch(getAllcatalogue);
    setShow(true);
  };

  //manuall bid data insert
  const addBid = async (el, index) => {
    const bidValue = amountBid[index]?.name || null;
    if (bidValue != null && el.maximum_bid < bidValue) {
      try {
        const bidData = {
          id: el.id,
          bid: bidValue,
          buyer: users.link_id,
        };

        await axios.put(`${API_URL}/manualbid`, bidData);

        const response = await axios.get(`${API_URL}/getselectbroker`);
        socket.emit("user", response.data[0].current_brokers);
      } catch (error) {
        console.log(error);
      }
    } else {
      alert("Amount less than maximum bid");
    }

    setManualmode(false);
  };
  let timer = null;

  // Visibility change handler
  const handleVisibilityChange = () => {
    if (document.visibilityState === 'visible') {
      clearTimeout(timer);
      axios
        .get(`${API_URL}/getselectbroker`)
        .then((response) => {
          if (response.data[0].auction === 0) {
            setShowPopup(true);
          } else {
            setShowPopup(false);
          }
        })
        .catch((error) => {
          console.error('Error fetching data:', error);
          // Handle the error appropriately (e.g., show an error message)
        });
    } else {
      timer = setTimeout(() => {
        navigate('/');
      }, 50000);
    }
  };
  
  useEffect(() => {
    // Add event listener for visibility change
    document.addEventListener('visibilitychange', handleVisibilityChange);
  
    return () => {
      clearTimeout(timer); // Clear the timer when the component unmounts
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, []);
  
  //bid update
  const handleInc = async (el, index) => {
    setDisabledButtons((prevDisabledButtons) => [
      ...prevDisabledButtons,
      index,
    ]);
    setbidLoading(true);
    const audio = new Audio(music);

    const updatedData = list.map((obj, idx) => {
      if (obj.id === el.id) {
        return {
          ...obj,
          maximum_bid: obj.maximum_bid + 1,
          buyer: users.link_id,
          key: idx,
        };
      }
      return { ...obj, key: idx };
    });

    const filteredData = updatedData.filter((item) => item.id === el.id);

    try {
      await axios.put(`${API_URL}/updateauction`, filteredData);
      socket.emit("user", activebroker);
    } catch (error) {
      console.log(error);
    } finally {
      setList(updatedData);
      audio.play();
      setbuttonLoading(false);
      setbidLoading(false);
      setDisabledButtons((prevDisabledButtons) =>
        prevDisabledButtons.filter((btnIndex) => btnIndex !== index)
      );
    }
  };

  //prev next index check
  useEffect(() => {
    
    const handleActiveUsers = (count) => {
      setActiveUsers(count);
    };
    const handleStartIndex = (data) => {
      setStartIndex(data);
    };
    const handleEndIndex = (data) => {
      setEndIndex(data);
    };

    socket.on("activeUsers", handleActiveUsers);
    socket.on("startIndex", handleStartIndex);
    socket.on("endIndex", handleEndIndex);
    socket.emit("getObjects");
    return () => {
      socket.off("activeUsers", handleActiveUsers);
      socket.off("startIndex", handleStartIndex);
      socket.off("endIndex", handleEndIndex);
    };
  }, []);

  const fetchData = async () => {
    let isMounted = true;
    try {
      const response = await axios.get(`${API_URL}/getselectbroker`);
      if (isMounted) {
        const { auction, current_brokers, name, id } = response.data[0];
        if (auction === 0) {
          setShowPopup(true);
        } else {
          setShowPopup(false);
        }

     

        Promise.all([
          setCheckedItemName(name),
          setCheckedItemid(id),
          setActivebroker(current_brokers),
        
        ]).then(() => {
          socket.emit("user", current_brokers);
          // socket.emit("user_toggle", current_brokers); need
          socket.on("user_toggle", (userId) => {
            setVisible(userId === users.link_id);
          });
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
  
    let isMounted = true; // Add a flag to track if the component is mounted

    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 1000);

    const handleStart = () => {
      setIsRunning(true);
    };

    const handleStop = () => {
      setIsRunning(false);
    };

    const handleTimeUpdate = (newTime) => {
      setTime(newTime);
    };
    // Clean up socket listeners
    socket.on("visibility", handleVisibilityChange);
    socket.on("start", handleStart);
    socket.on("stop", handleStop);
    socket.on("time", handleTimeUpdate);

    fetchData();

    socket.on("users", (data) => {
      if (isMounted) {
        // Update the state only if the component is still mounted
        const filteredData = data.filter((item) => item.sold_status === 0);
        setList(filteredData);
        setrawList(data);
      }
    });

    socket.on("checkname", (data) => {
      if (isMounted) {
        // Update the state only if the component is still mounted
        setCheckedItemName(data);
      }
    });

    return () => {
      isMounted = false; // Set the flag to false when the component is unmounted
      // Remove socket listeners
      socket.off("visibility", handleVisibilityChange);
      socket.off("start", handleStart);
      socket.off("stop", handleStop);
      socket.off("time", handleTimeUpdate);
      socket.off("users");
      socket.off("checkname");
      socket.off("user_toggle");
    };
  }, [socket]);

  useEffect(() => {
    let intervalId;
    if (isRunning) {
      // setShowPopup(false)
      const throttledTick = throttle(() => {
        socket.emit("tick");
      }, 1000);
      intervalId = setInterval(throttledTick, 1000);
    } else {
      // setShowPopup(true)
      clearInterval(intervalId); // Clear the interval if isRunning is false
    }

    return () => clearInterval(intervalId);
  }, [isRunning]);

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60)
      .toString()
      .padStart(2, "0");
    const seconds = (time % 60).toString().padStart(2, "0");
    return `${minutes}:${seconds}`;
  };

  const filteredData = rawlist.filter((item) => item.buyer === users.link_id);
  const filteredDatabrokers = rawlist.filter(
    (item) => item.brokers === checkedItemid && item.buyer != 0
  );



  return (
    <>
      {bidloading ? (
        <>
          <div className="overlay">
            <div className="spinner-container">
              <div className="spinner"></div>
              <span>Verifying data...</span>
            </div>
          </div>
        </>
      ) : (
        ""
      )}

      {loading && users.type !==""? (
        <>
          <div
            style={{
              height: "100vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <ReactLoading
              type={"spin"}
              color={"#007bff"}
              height={50}
              width={50}
            />
            <div
              style={{
                marginTop: "20px",
                fontSize: "16px",
                fontWeight: "bold",
              }}
            >
              Verifying Data...
            </div>
          </div>
        </>
      ) : (
        <>
          <Modal
            title="My Lot"
            onClose={() => setShow(false)}
            show={show}
            filteredData={
              users.type === "BROKERS" ? filteredDatabrokers : filteredData
            }
          ></Modal>

          <div className="section-head sec-h">
            <p className="br-n">={checkedItemName}=</p>
            <div className="row newc">
              <div className="col-7 col-md-9 card-btn leftCard">
                <h2>
                  Auction Date:{" "}
                  {auction_data.length === 0 ? "" : auction_data[0].date}{" "}
                </h2>
                <p>
                  SALE NO - #
                  {auction_data.length === 0 ? "" : auction_data[0].sale_no}{" "}
                  {activeUsers}{" "}
                </p>
                <p>
                  Season - #
                  {auction_data.length === 0 ? "" : auction_data[0].season}{" "}
                </p>
                <p>
                  CURRENT BROKER :{" "}
                  <span className="c_brokers">{checkedItemName}</span>{" "}
                </p>

                <div className=" row margin-left">
                  <button
                    type="button"
                    onClick={openLot}
                    className="col-5 buttonGroup btn btn-success my-btn"
                  >
                    My Lot
                  </button>
                </div>
              </div>
              <div className="col-5 col-md-3 align-items-center justify-content-center mt-5 rCard">
                <div className="col-12">
                  <div className="card">
                    <div className="row rightCard">
                      <div className=" ">
                        <div className="col-md-6 count-t">
                          <h2 className="text-center mh2">
                            {formatTime(time)}
                          </h2>
                        </div>
                        <div className="col-md-6 name-t">
                          <h2 className="mh2 text-center">
                            {users.short_name}
                          </h2>
                          
                        </div>
                      </div>
                    </div>
                    <div>
                    <div className="col-md-12 name-t mh3">
                          <h4 className=" text-center">
                          Remaining : {list.length} Lot
                          </h4>
                          
                        </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {showPopup && users.type === "BIDDER" ? (
            <>
              <h2 className="text-center  p-h">-={users.type} PANEL=-</h2>
              <Popup />
            </>
          ) : (
            <>
              <h2 className="text-center p-h">-={users.type} PANEL=-</h2>
              <hr className="hr-h" />
              <LotListComponent
                time={time}
                list={list}
                startIndex={startIndex}
                endIndex={endIndex}
                users={users}
                formatTime={formatTime}
                manualmode={manualmode}
                selectedIndex={selectedIndex}
                handleInputChange={handleInputChange}
                addBid={addBid}
                handleInc={handleInc}
                disabledButtons={disabledButtons}
                setManualmode={setManualmode}
                setSelectedIndex={setSelectedIndex}
              />
            </>
          )}
        </>
      )}
    </>
  );
};

export default Bidders;
