import React from 'react'
import {
    FaHandPaper,
    FaRegWindowClose,
    FaRegCheckSquare,
  } from "react-icons/fa"
const BidderLotComponent = ({
  index,
    time,
    el,
    users,
    formatTime,
    manualmode,
    selectedIndex,
    handleInputChange,
    addBid,
    handleInc,
    disabledButtons,
    setSelectedIndex,
    setManualmode
  }) => {
    return (
      <div className="col-md-3 col-sm-12 col-xs-12 list">
        <div className="card mb-4">
          <div className="badge">Lot- {el.lot}</div>
  
          <div className="card-body ">
            <span className="text-white bg-l px-2 py-1 rounded-pill animate__animated animate__flash animate__infinite">
              Live {formatTime(time)}
              <span className="dot"></span>
            </span>
  
            <div className="mt-3 row">
            <div className="col-md-7 col-sm-7 col-xs-7 border-right">
  <div className="row">
    <div className="col custom-bg-1 custom-padding">
      <p className="header-text text-center">FACTORY</p>
    <p className="card-text text-center">{el.factory_name}</p>
    
 </div>
    <div className="col custom-bg-2 custom-padding">
  <p className="header-text text-center">INVOICE</p>
      <p className="card-text text-center">{el.invoice}</p>
    </div>
  </div>
  <div className="row">
    <div className="col custom-bg-3 custom-padding">
  <p className="header-text text-center">LOT</p>
      <p className="card-text text-center">{el.lot}</p>
    </div>
    <div className="col custom-bg-4 custom-padding">
   <p className="header-text text-center">PRICE</p>
      <p className="card-text text-center">{el.price}</p>
    </div>
  </div>
  <div className="row">
    <div className="col custom-bg-5 custom-padding">
  <p className="header-text text-center">QTY</p>
      <p className="card-text text-center">{el.pkgs}</p>
    </div>
    <div className="col custom-bg-6 custom-padding">
   <p className="header-text text-center">GRADE</p>
      <p className="card-text text-center">{el.grade}</p>
    </div>
  </div>
               <div className='row mxcom'>
                
             
                  {manualmode === true && selectedIndex === index ? (
                    <div className="mn-bid col-12">
                      <h5 className="card-price">Max.{el.maximum_bid}</h5>
                      <input
                        className="form-control m_bid"
                        autoComplete="off"
                        type="number"
                        id={`name${el.id}`}
                        name="name"
                        value={el.name}
                        onChange={(e) => handleInputChange(e, index)}
                      />
                    </div>
                  ) : el.maximum_bid === 0 ? (
                    <div className="mn-bid col-12">
                      <h5 className="card-price">Max.{el.maximum_bid}</h5>
                      <input
                        className="form-control m_bid"
                        autoComplete="off"
                        type="number"
                        id={`name${el.id}`}
                        name="name"
                        value={el.name}
                        onChange={(e) => handleInputChange(e, index)}
                      />
                    </div>
                  ) : (
                    <>
                      <h5 className="card-price">Max.{el.maximum_bid}</h5>
                      <span
                        className={
                          el.buyer === users.link_id ? "hname" : "rname"
                        }
                      >
                        {el.buyer_name}({el.maximum_bid - el.price})
                      </span>
                      <br />
                    </>
                  )}
                </div>
              </div>
  
              <div className="col-md-5 d-flex justify-content-center align-items-center col-sm-5 col-xs-5 b-bg">
                {el.maximum_bid === 0 ? (
                  <button
                    key={el.id + 10}
                    disabled={users.type === "BIDDER" ? false : true}
                    onClick={() => addBid(el, index)}
                    className="btn btn-danger square-circle"
                  >
                    <FaHandPaper className="bid" />
                  </button>
                ) : (
                  <div className="col-12 b-bg">
                    <div className="bidcont mb-2">
                      <button
                        key={el.id + 11}
                        disabled={disabledButtons.includes(index)}
                        onClick={() => handleInc(el, index)}
                        className="btn btn-danger square-circle bidbtn"
                      >
                        <FaHandPaper className="bid" /> +1
                      </button>
                      <button
                        key={el.id}
                        onClick={() => {
                        
                          if (manualmode === true && selectedIndex === index) {
                            addBid(el, index);
                          } else {
                            setManualmode(true);
                            setSelectedIndex(index);
                          }
                        }}
                        className="btn btn-primary square-circle bidbtn"
                      >
                        <FaHandPaper className="bid" /> BID
                      </button>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };
  
export default BidderLotComponent