import React from 'react'
import BidderLotComponent from './BidderLotComponent';
const LotListComponent = ({
    time,
    list,
    startIndex,
    endIndex,
    users,
    formatTime,
    manualmode,
    selectedIndex,
    handleInputChange,
    addBid,
    handleInc,
    disabledButtons,
    setManualmode,
    setSelectedIndex
  }) => {
    return (
       
      <div className="con">
        <div className="row g-1">
           
          {list.slice(startIndex, endIndex + 3).map((el, index) => (
            <BidderLotComponent
            time={time}
              key={el.id + 9}
              el={el}
              index={index}
              setManualmode={setManualmode}
              setSelectedIndex={setSelectedIndex}
              users={users}
              formatTime={formatTime}
              manualmode={manualmode}
              selectedIndex={selectedIndex}
              handleInputChange={handleInputChange}
              addBid={addBid}
              handleInc={handleInc}
              disabledButtons={disabledButtons}
            />
          ))}
        </div>
      </div>
    );
  };
  

export default LotListComponent